<template lang="html">
    <div class="container mt-5 px-2">
      <p>The <strong>26th UN Climate Change Conference of the Parties (COP26)</strong> took place in Glasgow on 31 October – 12 November 2021.</p>
      <p>Panels where experts discussed challenges for communities, but also business and governmental were streamed op YouTube.</p>
      <p>However, these panels don't boast with an exceptional large number of viewers. This overview aims to help finding interesting panels to watch.</p>
      <p>Table contains two merged datasets:</p>
    <ul>
      <li><a target="_blank" href="https://ukcop26.org/the-conference/green-zone-programme-of-events/">COP26 program information</a> (title & organizer)</li>
      <li>links to <a target="_blank" href="https://www.youtube.com/c/COP26/videos">past YouTube streams (views count as of 12 November 2021)</a></li>
    </ul>

      <p> Sessions are between 1 and 1.5 hours long.</p>
      <p>🎥 = contains movies</p>

    <div class="table-responsive">
    <table class="table table-hover">
      <thead>
      <tr>
        <th scope="col"></th>
        <th scope="col">Title</th>
        <th scope="col" @click="sort('organizer')">Organizer &#8645;</th>
        <th scope="col">Video</th>
        <th scope="col" @click="sort('views')">Views &#8645;</th>
        <th scope="col">Description</th>
      </tr>
    </thead>
    <tbody>
        <tr v-for="row in sortedTable" :key="data.ID">
            <CatalogueItem :row="row" />
        </tr>
    </tbody>

  </table>
</div>
  <p>
    Data merging, cleaning and publishing excercise by Adriana Homolova. Get in touch? <a class="font-weight-bold" href='&#109;ail&#116;o&#58;adria&#110;&#37;61&#64;&#104;omolov%6&#49;&#46;s&#107;'>&#97;drian&#97;&#64;ho&#109;ol&#111;va&#46;sk</a>
  </p>
</div>
</template>

<script>
import CatalogueItem from './CatalogueItem.vue'

export default {
  props: ['data'],
  components: {CatalogueItem},
  data() {
    return {
      currentSort:'views',
      currentSortDir:'desc'
    }
  },
  methods:{
  sort:function(s) {
    //if s == current sort, reverse
    if(s === this.currentSort) {
      this.currentSortDir = this.currentSortDir==='asc'?'desc':'asc';
    }
    this.currentSort = s;
  }
},

  computed:{
  sortedTable:function() {
    return this.data.sort((a,b) => {
      let modifier = 1;
      if(this.currentSortDir === 'desc') modifier = -1;
      if(a[this.currentSort] < b[this.currentSort]) return -1 * modifier;
      if(a[this.currentSort] > b[this.currentSort]) return 1 * modifier;
      return 0;
    });
  }
}


}
</script>

